// src/app/layout.tsx
import './globals.css'
import Header from '@/components/Header'
import NeuralScene from './three/NeuralCore'

export const metadata = {
  title: 'Giel Nijkamp',
  description: 'tbd',
}

export default function RootLayout({
  children,
}: {
  children: React.ReactNode
}) {
  return (
    <html lang="en">
      {/* 
         Zet hier eventueel je head-tags of meta-data.
         De achtergrondkleur regelen we met Tailwind (bg-black) 
         en we zetten de NeuralScene gefixt achter alles.
       */}
      <body className="relative min-h-screen bg-black text-white">
        {/* De Three.js-scene als vaste achtergrond */}
        <div className="fixed inset-0 -z-10">
          <NeuralScene />
        </div>

        {/* De rest van je pagina-content */}
        {children}
      </body>
    </html>
  )
}